  inChuyenDoi(data) {
    if (this.selectedItems.length || data) {
      let _lst = this.selectedItems.filter(p => p.status == 'DA_XUAT');
      if (_lst.length > 0 || data) {
        let ids = [];
        if (data) {
          if (data.status == 'DA_XUAT') {
            ids.push(data.id);
          } else {
            Library.notify(this._translate.NOTIFY.not_print_convert, 'error');
            return;
          }
        } else {
          ids = _lst.map(e => e.id);
        }
        if (data.xml_type == 4 && VStringService._getValueToString(data.link_pdf_file) != "") {
          let oData =
          {
            url: data.link_pdf_file,
            type: 'pdf'
          }
          let popup = window.open('about:blank', 'name', `width=200, height=100`);
          popup.document.body.innerHTML = this._translate.NOTIFY.loading;
          popup.blur();
          this.spinner.show();
          const sub = this.searchService.getFilePdf(oData).subscribe(rs => {
            if (rs.status === 'success') {
              let w = 1920, h = 900;
              w = window.screen.availWidth * 100 / 100;
              h = window.screen.availHeight * 93 / 100;
              const pdf_file = rs.data.file_content;
              const objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
              const win = window.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
              win.resizeTo(screen.availWidth, screen.availHeight);
              win.focus();
              this.spinner.hide();
            } else {
              popup.close();
              this.spinner.hide();
              Library.notify(rs.message, 'error');
            }
          }, err => {
            popup.close();
            this.spinner.hide();
          }, () => {
            sub.unsubscribe();
          });
        }
        else {
          if (this.configs.system_parameter.view_multi_invoice) {
            const data = this.vatService.taodulieuInMau(ids, false);
            data['configs'] = sEnum.cauhinhxemmau.hoadon;
            this.vPreviewInvService.xemMau(data);
          }
          else {
            const initialState = {
              items: ids,
              isXuatHD: false,
              hinhthucxuat: '',
              typeAction: sEnum.typeCode.in_chuyen_doi_hd
            };
            this.vPreviewInvService.xemMauPhanTrang(initialState);
          }
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }

    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

