import { Component, OnInit } from '@angular/core';
import { Library, Helper, VChanggeMoneyService, AuthService, VStringService } from '@core/index';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { InfoUnitService, InfoUnit } from '@modules/info-unit/shared';
import * as moment from 'moment';
import { VatService } from '@modules/vat/shared';

@Component({
  selector: 'app-report-cancel-inv',
  templateUrl: './report-cancel-inv.component.html',
  styleUrls: ['./report-cancel-inv.component.scss']
})
export class ReportCancelInvComponent implements OnInit {
  _translate: any;
  customer: any;
  frmData: any;
  frmDataClone: any;
  oldForm: any;
  _objTemplateSelect: any;
  benB: any = {};
  fileData: any;
  invoice_date = "";
  ngayVB = "";

  constructor(private authService: AuthService,
    public bsModalRef: BsModalRef,
    private infoUnitService: InfoUnitService,
    private vatService: VatService,

  ) {
    this._translate = this.authService.getTranslate();

  }

  ngOnInit() {
    this.frmDataClone = Object.assign({}, this.frmData);
    //this.customer.name = "CNG HA X HI CH NGHA VIT NAM";
    this.invoice_date = "ngy " + moment(this.oldForm.invoice_date).format('DD')
      + " thng " + moment(this.oldForm.invoice_date).format('MM')
      + " nm " + moment(this.oldForm.invoice_date).format('YYYY');
    this.ngayVB = "ngy " + moment(this.frmData.doc_relate_date).format('DD')
      + " thng " + moment(this.frmData.doc_relate_date).format('MM')
      + " nm " + moment(this.frmData.doc_relate_date).format('YYYY');
    this.getUnitInfo();
  }
  getUnitInfo() {
    let accountId = this.authService.getCurrentUser().account.id;
    if (accountId) {
      this.infoUnitService.getInfo(accountId).subscribe((res) => {
        delete res.data.account.van_account_info;
        this.benB = res.data.account;
      })
    }
  }
  onSubmit(e) {
    Library.showloading();
    let inv = Object.assign({}, this.oldForm);
    inv.doc_relate_date = this.frmData.doc_relate_date;
    inv.doc_relate_symbol = this.frmData.doc_relate_symbol;
    inv.destroy_reason = this.frmData.destroy_reason;

    inv.invoice_date_new = this.frmData.invoice_date;
    inv.invoice_number_new = this.frmData.invoice_number;
    inv.template_code_new = this._objTemplateSelect.template_code;
    inv.invoice_series_new = this._objTemplateSelect.invoice_series;

    inv.adjustment_type = "1";
    inv.auto_value_price = inv.auto_value_price == null ? false : inv.auto_value_price;
    //inv.invoice_products =this.oldForm.invoice_products;
    // this.dataSource.items().forEach(item => {
    //   if (item.product_name)
    //     inv.invoice_products.push(item);
    // });
    inv.invoice_files = [];
    this.vatService.xuatMauBienBanThuHoi(inv).subscribe(rs => {
      if (rs.status == 'success') {
        let file = rs.data.invoice;
        let _attachFile = this.fileData.map(e => e.file_name);
        let _index = _attachFile.indexOf(file.file_name)
        if (_index == -1) {
          this.fileData.push({ type: "DK", file_name: file.file_name, file_content: file.file_content });
          //Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
        } else {
          this.fileData.splice(_index, 1);
          this.fileData.push({ type: "DK", file_name: file.file_name, file_content: file.file_content });
        }
        this.bsModalRef.hide();
        Library.notify("Xut bin bn thu hi thnh cng", 'success');

      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }
  dong() {
    //this.isValid = false;
    this.frmData.invoice_date = this.frmDataClone.invoice_date;
    this.frmData.invoice_number = this.frmDataClone.invoice_number;
    this.bsModalRef.hide();
  }
}
