  clone(): any {
    const cloneObj = new (<any>this.constructor());
    for (const attribut in this) {
      if (typeof this[attribut] === 'object') {
        cloneObj[attribut] = this.clone();
      } else {
        cloneObj[attribut] = this[attribut];
      }
    }
    return cloneObj;
  }

