  showModalHdck() {
    const initialState = {
      invoiceNumberOrigin: this.oldForm.invoice_number,
      invoiceDateOrigin: this.oldForm.invoice_date,
      invoiceReleaseId: this.frmData.invoice_release_id,//this.oldForm.invoice_release_id,
      onSave: (data) => {
        this.frmData.status = sEnum.statusInv.ChoKy;
        this.frmData.id = data.id;
        this.frmData.invoice_date = data.invoice_date;
        this.frmData.invoice_number = data.invoice_number;
      }
    };
    this.bsModalRef = this.modalService.show(ChooseHdckComponent, {
      backdrop: 'static',
      keyboard: false,
      class: 'modal-lg full-modal',
      initialState
    });
  }

  capNhatDmKhachHang() {
    this.customerCurent.tax_code = VStringService._getValueToString(this.frmData.buyer_tax_code);
    this.customerCurent.name = VStringService._getValueToString(this.frmData.customer_name);
    this.customerCurent.code = VStringService._getValueToString(this.frmData.customer_code);
    this.customerCurent.contact_person_name = VStringService._getValueToString(this.frmData.buyer_name);
    this.customerCurent.contact_mobile = VStringService._getValueToString(this.frmData.buyer_mobile);
    this.customerCurent.business_address = VStringService._getValueToString(this.frmData.buyer_address);
    this.customerCurent.email = VStringService._getValueToString(this.frmData.buyer_email);
    this.customerCurent.bank_name = VStringService._getValueToString(this.frmData.bank_name);
    this.customerCurent.bank_account_number = VStringService._getValueToString(this.frmData.bank_account_number);
    const data = { customer: this.customerCurent };
    this.customersService.edit(data, this.customerCurent.id).subscribe((response: any) => {
      if (response.status === 'success') {
        this.showPopup = false;
      } else {
        Library.notify(response.message, 'error');
      }
    }, err => {
    }, () => {
    });

  }

  huy() {
    this.showPopup = false;
  }

  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          this.arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.frmData.buyer_email = emailToString.replace(/,/g, ';');
    } else {
      this.frmData.buyer_email = null;
    }
    this.formEvent.emit(this.frmData);
  }
  updateCustomerInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.code) !== VStringService._getValueToString(this.frmData.customer_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  }

  updateTaxcodeInfo = (e) => {
    if (this.frmData.customer_id) {
      this.customersService.getCustomer(this.frmData.customer_id).subscribe(response => {
        if (response.data) {
          this.customerCurent = response.data.customer;
          if (this.customerCurent) {
            if (VStringService._getValueToString(this.customerCurent.tax_code) !== VStringService._getValueToString(this.frmData.buyer_tax_code)) {
              this.showPopup = true;
              return;
            }
          }
        }
      });
    }
  }
  changeDateTime = (e) => {
    this.changeTemplateInvoice(e.value);
  }

  changeTemplateInvoice(strDate) {
    if (this.isTT78) {
      let year = "";
      if (VStringService._getValueToString(strDate) != "")
        year = VStringService._getValueToString(strDate).substring(4, 2);
      else {
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        year = VStringService._getValueToString(invoiceDate).substring(4, 2);
      }
      let templates = this.select.templatesTT78;
      this.select.templatesTT78 = [];
      if (templates && templates.length > 0) {
        templates.forEach(item => {
          if (VStringService._getValueToString(item.name) != "")
            item.name = item.name.replace(item.name.substring(4, 2), year);
          // for (const key in item) {
          //   if (item.hasOwnProperty(key)) {
          //     const value = item[key];
          //     this._oTemplateTT78[key] = value;
          //   }
          // }
          if (item.value === this.frmData.template_id) {
            this.onTemplateTT78TypeChange.emit(item);
            //  item.invoice_series = item.invoice_series.replace(item.invoice_series.substring(3, 1), year);
            // this.frmData.invoice_series = item.invoice_series;
            // this.frmData.template_code = item.template_code;
          }
          this.select.templatesTT78.push(item);
        });
      }
    }
  }

  disableTemplateInput() {
    if (this.frmData.invoice_number) {
      return true;
    }
    return false;
  }
  changeTaxCode(e) {
    this.tax_code_search = e.value
  }
  searchTaxCode() {
    Library.showloading();
    if (!this.tax_code_search) {
      Library.notify("Vui lng nhp m s thu!", "error")
    } else {
      this.vatService.getInfoCompany(this.tax_code_search).subscribe(res => {
        if (res.status == "success") {
          this.dataTaxCode = res.data.customer;
          this.frmData.customer_name = this.dataTaxCode.name;
          this.frmData.buyer_address = this.dataTaxCode.business_address;
        }
      })
    }
    this.onFieldDataChanged();
    setTimeout(() => {
      Library.hideloading();
    }, 1000);
  }
