  adjustmentObjectChange(v) {
    this.doctienbangchu(this.frmData.total_payment, this.frmData.currency_code);
    let _noiDungDC = '';
    let isDC = false;
    switch (v) {
      case 'TANG_HH_DV':
        this.setContentProductNameV2('', 'select');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false, 'TANG_HH_DV');
        break;
      case 'TANG_KHAC_TANG':
        this.setContentProductName('', 'text');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false);
        break;
      case 'GIAM_HH_DV':
        this.setContentProductNameV2('', 'select');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false, 'GIAM_HH_DV');
        break;
      case 'GIAM_KHAC_GIAM':
        this.setContentProductName('', 'text');
        this.productGrip.gripAllowEdit = true;
        this.productGrip.ShowAndHidePhuongThucDC(false);
        break;
      case 'THONG_TIN_MST':
        isDC = true;
        if (this.frmData.buyer_tax_code && this.oldForm.buyer_tax_code !== this.frmData.buyer_tax_code) {
          _noiDungDC = `iu chnh m s thu Cng ty/n v mua hng t ${this.oldForm.buyer_tax_code} thnh ${this.frmData.buyer_tax_code}`;
        }
        break;
      case 'THONG_TIN_KH_DC':
        isDC = true;
        if (this.frmData.customer_name && this.oldForm.customer_name !== this.frmData.customer_name) {
          _noiDungDC = `iu chnh tn n v mua hng t ${this.oldForm.customer_name} thnh ${this.frmData.customer_name}`;
        }
        if (this.frmData.buyer_name && this.oldForm.buyer_name !== this.frmData.buyer_name) {
          if (_noiDungDC) {
            _noiDungDC += '\n';
          }
          _noiDungDC += `iu chnh tn ngi mua t ${this.oldForm.buyer_name} thnh ${this.frmData.buyer_name}`;
        }
        if (this.frmData.buyer_address && this.oldForm.buyer_address !== this.frmData.buyer_address) {
          if (_noiDungDC) {
            _noiDungDC += '\n';
          }
          _noiDungDC += `iu chnh a ch ca n v mua hng t ${this.oldForm.buyer_address} thnh ${this.frmData.buyer_address}`;
        }
        break;
      case 'THONG_TIN_TIEN_CHU':
        isDC = true;
        if (this.frmData.total_payment_in_word && this.oldForm.total_payment_in_word !== this.frmData.total_payment_in_word) {
          let invoice_date = moment(this.oldForm.invoice_date).format('DD/MM/YYYY');
          _noiDungDC = `iu chnh dng s tin bng ch ca ha n s ${this.oldForm.invoice_number}, k hiu ${this.oldForm.invoice_series} ngy ${invoice_date} t: ${this.oldForm.total_payment_in_word} thnh ${this.frmData.total_payment_in_word}`;
        }
        break;
      case 'KHAC_KHAC':
        setTimeout(() => {
          this.productGrip.changeColumnProductNameToText();
        }, 100);
        this.productGrip.ShowAndHidePhuongThucDC(false);
        this.productGrip.gripAllowEdit = true;
        break;
      case 'TANG_GIAM':
        this.setContentProductName('', 'text');
        if (!this.isTT78)
          this.productGrip.ShowAndHidePhuongThucDC(true);
        else
          this.productGrip.ShowAndHidePhuongThucDC(false);
        this.productGrip.gripAllowEdit = true;
        break;
      default:
        break;
    }
    if (isDC) {
      this.setContentProductName(_noiDungDC, 'text');
      this.productGrip.gripAllowEdit = false;
      this.productGrip.ShowAndHidePhuongThucDC(false);
    }
  }

  setContentProductName(value, type) {
    setTimeout(() => {
      if (this.productGrip.dataSource.items()[0]) {
        if (!this.configs.system_parameter.is_add_product_code) {
          this.productGrip.dataSource.items()[0].product_name = value;
        }
      }
      if (type === 'text') {
        this.productGrip.changeColumnProductNameToText();
      } else if (type === 'select') {
        this.productGrip.changeColumnProductNameToSelect();
      }
    }, 100);
  }

  setContentProductNameV2(value, type) {
    setTimeout(() => {
      if (type === 'text') {
        this.productGrip.changeColumnProductNameToText();
      } else if (type === 'select') {
        this.productGrip.changeColumnProductNameToSelect();
      }
    }, 100);
  }

  /** Kt thc */

  roundNumber(number, n): number {
    if (number == null) {
      return number;
    } else {
      let p = Math.pow(10, n);
      return Math.round(number * p) / p;
    }
  }


  formatDecimalColumn(number) {
    if (number === 0) {
      return '0,###';
    } else {
      return '0,###.' + '0'.repeat(number);
    }
  }

