import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndexComponent } from './components/index/index.component';
import { AdjustInvoiceComponent } from './components/adjust/adjust-invoice/adjust-invoice.component';
import { ReplaceInvoiceComponent } from './components/replace/replace-invoice/replace-invoice.component';

import { AdjustInvoiceResolver, ReplaceInvoiceResolver, AdjustSaleInvoiceResolver, ReplaceSaleInvoiceResolver } from './shared';
import { VatDeactivateGuard } from '@modules/vat/deactivate/vat-deactivate.guard';
import { AdjustMultipleInvoiceResolver } from './shared/adjust-multiple-invoice.resolver';
import { AdjustMultiplInvoiceComponent } from './components/adjust-multiple/adjust-multipl-invoice/adjust-multipl-invoice.component';
import { ShowAdjustMultipleInvoiceResolver } from './shared/show-adjust-multiple-invoice.resolver';


const routes: Routes = [
  { path: '', component: IndexComponent },
  {
    path: 'adjust/:originId',
    component: AdjustInvoiceComponent,
    resolve: { init: AdjustInvoiceResolver },
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '01GTKT',
      brec: 'create_invoice_adjustment',
      status: 'dieuchinhmoi'
    }
  },
  {
    path: 'replace/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: {
      type: '01GTKT',
      brec: 'create_invoice_instead',
      status: 'thaythemoi'
    }
  },
  {
    path: 'edit-adjust/:invoiceId',
    component: AdjustInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: AdjustInvoiceResolver },
    data: {
      type: '01GTKT',
      brec: 'create_invoice_adjustment'
    }
  },
  {
    path: 'edit-replace/:invoiceId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: {
      type: '01GTKT',
      brec: 'create_invoice_instead'
    }
  },
  {
    path: 'sale-adjust/:originId',
    component: AdjustInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: AdjustInvoiceResolver },
    data: {
      type: '02GTTT',
      brec: 'create_invoice_adjustment',
      status: 'dieuchinhmoi'
    }
  },
  {
    path: 'non-tariff-adjust/:originId',
    component: AdjustInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: AdjustInvoiceResolver },
    data: {
      type: '07KPTQ',
      brec: 'create_invoice_adjustment',
      status: 'dieuchinhmoi'
    }
  },
  {
    path: 'sale-replace/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: { type: '02GTTT', brec: 'create_invoice_instead', status: 'thaythemoi' }
  },
  {
    path: 'non-tariff-replace/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: { type: '07KPTQ', brec: 'create_invoice_instead', status: 'thaythemoi' }
  },
  {
    path: 'edit-sale-adjust/:invoiceId',
    component: AdjustInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: AdjustInvoiceResolver },
    data: { type: '02GTTT', brec: 'create_invoice_adjustment' }
  },
  {
    path: 'edit-non-tariff-adjust/:invoiceId',
    component: AdjustInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: AdjustInvoiceResolver },
    data: { type: '07KPTQ', brec: 'create_invoice_adjustment' }
  },
  {
    path: 'edit-sale-replace/:invoiceId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: { type: '02GTTT', brec: 'create_invoice_instead' }
  },
  {
    path: 'edit-non-tariff-replace/:invoiceId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: { type: '07KPTQ', brec: 'create_invoice_instead' }
  },
  {
    path: 'multiple-adjust',
    component: AdjustMultiplInvoiceComponent,
    resolve: { init: AdjustMultipleInvoiceResolver },
    data: { type: 'dieu-chinh-nhieu', brec: 'Lp ha n iu chnh' }
  },
  {
    path: 'multiple-adjust/:invoiceId',
    component: AdjustMultiplInvoiceComponent,
    resolve: { init: ShowAdjustMultipleInvoiceResolver },
    data: { type: 'dieu-chinh-nhieu', brec: 'Lp ha n iu chnh' }
  },
  {
    path: 'adjust-without-software',
    component: AdjustMultiplInvoiceComponent,
    resolve: { init: AdjustMultipleInvoiceResolver },
    data: { type: 'dieu-chinh-khac', brec: 'Lp ha n iu chnh' }
  },
  {
    path: 'adjust-without-software/:invoiceId',
    component: AdjustMultiplInvoiceComponent,
    resolve: { init: ShowAdjustMultipleInvoiceResolver },
    data: { type: 'dieu-chinh-khac', brec: 'Lp ha n iu chnh' }
  },
  {
    path: 'replace-without-software',
    component: AdjustMultiplInvoiceComponent,
    resolve: { init: AdjustMultipleInvoiceResolver },
    data: { type: 'thay-the-khac', brec: 'Lp ha n thay th' }
  },
  {
    path: 'replace-without-software/:invoiceId',
    component: AdjustMultiplInvoiceComponent,
    resolve: { init: ShowAdjustMultipleInvoiceResolver },
    data: { type: 'thay-the-khac', brec: 'Lp ha n thay th' }
  },
  {
    path: 'adjust-warehouse/:originId',
    component: AdjustInvoiceComponent,
    resolve: { init: AdjustInvoiceResolver },
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '03XKNB',
      brec: 'create_invoice_adjustment',
      status: 'dieuchinhmoi'
    }
  },
  {
    path: 'replace-warehouse/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: {
      type: '03XKNB',
      brec: 'create_invoice_instead',
      status: 'thaythemoi'
    }
  },
  {
    path: 'adjust-agent/:originId',
    component: AdjustInvoiceComponent,
    resolve: { init: AdjustInvoiceResolver },
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '04HGDL',
      brec: 'create_invoice_adjustment',
      status: 'dieuchinhmoi'
    }
  },
  {
    path: 'replace-agent/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: {
      type: '04HGDL',
      brec: 'create_invoice_instead',
      status: 'thaythemoi'
    }
  },
  {
    path: 'edit-adjust-warehouse/:originId',
    component: AdjustInvoiceComponent,
    resolve: { init: AdjustInvoiceResolver },
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '03XKNB',
      brec: 'create_invoice_adjustment',
    }
  },
  {
    path: 'edit-adjust-agent/:originId',
    component: AdjustInvoiceComponent,
    resolve: { init: AdjustInvoiceResolver },
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '04HGDL',
      brec: 'create_invoice_adjustment',
    }
  },
  {
    path: 'edit-replace-warehouse/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: {
      type: '03XKNB',
      brec: 'create_invoice_instead',
    }
  },
  {
    path: 'edit-replace-agent/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ReplaceInvoiceResolver },
    data: {
      type: '04HGDL',
      brec: 'create_invoice_instead',
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class routing {
}
