  khoiTaoKhiSua(data) {
    // khi to li t d liu sa
    const _lst = this.frmData.setInvoice_products_data(data.invoice_products);
    if (_lst) {
      _lst.forEach(item => {
        if (item.product_name && item.unit_name) {
          const obj1 = this.select.unit_codes.filter(p => p.name === item.unit_name)[0];
          if (!obj1) {
            if (VStringService._getValueToString(item.unit_code) == "")
              item.unit_code = VStringService._convertToViKdau(item.unit_name);
            let oDuplicate = this.select.unit_codes.filter(p => p.code === item.unit_code)[0];
            if (oDuplicate)
              item.unit_code = item.unit_code + Helper.addtionalUnitCode();
          }
          else
            if (!item.unit_code || (item.unit_code && item.unit_code != obj1.code))
              item.unit_code = obj1.code;
        }
      });
    }
    this.invoice_products = new ArrayStore({
      key: 'id',
      data: _lst
    });
    this.dataSource = new DataSource({
      store: this.invoice_products
    });

    // mu nhiu loi thu sut ko


    // khi to d liu form
    this.frmData.fillFormData(data);
    if (VStringService._getValueToString(this.frmData.adjustment_object) == "TANG_GIAM" && !this.isTT78) {
      this.productGrip.ShowAndHidePhuongThucDC(true);
    }
    if (this.frmData.vat_service_ticket != null) {
      this.frmData.vat_service_ticket = this.frmData.vat_service_ticket.toString();
    }
    // this.is_multi_vat = data.is_multi_vat;

    // ly file
    if (this.vatService.isCopy) {
      this.frmData.time_print_conversion = null;
      this.fileData = [];
      if (!this.isTT78) {
        const _objRelease = this.select.invoice_releases.filter(p => p.value === this.frmData.invoice_release_id)[0];
        if (_objRelease) {
          if (_objRelease.number_survival === 0) {
            const _lstReleaseOther = this.select.invoice_releases.filter(p => p.number_survival > 0 && p.invoice_series == _objRelease.invoice_series && p.template_code == _objRelease.template_code && p.release_status != 'DA_HUY_SO');
            if (_lstReleaseOther && _lstReleaseOther.length > 0) {
              this.frmData.invoice_release_id = _lstReleaseOther[0].value;
              this.frmData.template_id = _lstReleaseOther[0].template_id;
            } else {
              this.frmData.invoice_release_id = null;
              this.frmData.template_id = null;
            }
          }
        }
        this.select.invoice_releases = this.select.invoice_releases.filter(p => p.number_survival > 0 && p.release_status !== 'DA_HUY_SO');
      }
      else {
        let _oTempt = this.select.templatesTT78.filter(p => p.value === this.frmData.template_id)[0];
        if (_oTempt) {
          this.frmData.template_id = _oTempt.value;
          this.frmData.invoice_series = _oTempt.invoice_series;
          this.frmData.template_code = _oTempt.template_code;
        }
        else {
          this.frmData.template_id = null;
          this.frmData.invoice_series = null;
          this.frmData.template_code = null;
        }
      }
    } else {
      this.fileData = data.invoice_files;
    }
    if (!this.isTT78) {
      this.select.invoice_releases.forEach(item => {
        if (item.template_id === this.frmData.template_id) {
          this.invoicereleaseTempt = item;
        }
      });
      this.select.invoice_releases.some(item => {
        if (item.value === this.frmData.invoice_release_id) {
          this.is_multi_vat = item.is_multi_vat;
          //  this._objTemplateSelect = item; // khng m(i tng _objTemplateSelect b chuyn thnh i tng item)
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              this._objTemplateSelect[key] = item[key];
            }
          }
          return true;
        }
      });
    }
    else {
      this.select.templatesTT78.forEach(item => {
        if (item.value === this.frmData.template_id) {
          this.invoiceTemplateTT78 = item;
        }
      });
      this.select.templatesTT78.some(item => {
        if (item.value === this.frmData.template_id) {
          this.is_multi_vat = item.is_multi_vat;
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              this._oTemplateTT78[key] = item[key];
            }
          }
          return true;
        }
      });
    }
    if (VStringService._getValueToString(this.frmData.approve_status) == sEnum.statusApprove.TuChoi) {
      this.isTuChoi = true;
    }
    if (this.frmData.status === sEnum.statusInv.DaXuat || this.frmData.status === sEnum.statusInv.ChoKy)
      this.isShowButtonWaiteSign = false;
    // cu hnh thp phn thm mi
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(JSON.parse(data.decimal_places_json));

  }

