  /** Kt thc */

  roundNumber(number, n): number {
    if (number == null) {
      return number;
    } else {
      const p = Math.pow(10, n);
      return Math.round(number * p) / p;
    }
  }

  getFormatterToNumber(formatter) {
    const s1 = formatter.replace('0,###', '');
    if (s1 === '') {
      return 0;
    } else {
      const s2 = s1.replace('.', '');
      return s2.split('').length;
    }
  }

  formatDecimalColumn(number) {
    if (number === 0) {
      return '0,###';
    } else {
      return '0,###.' + '0'.repeat(number);
    }
  }

  inChuyenDoiText() {
    if (this.frmData.time_print_conversion > 0) {
      return this._translate.CONTROLL.BUTTON.view_conversion;
    } else {
      return this._translate.CONTROLL.BUTTON.print_conversion;
    }
  }

