  xemhoadonPDF(rs) {
    //Xem ha n t server
    if (this.configs.system_parameter.display_template_from_server) {
      let w = 1920, h = 900;
      w = window.screen.availWidth * 100 / 100;
      h = window.screen.availHeight * 93 / 100;
      const pdf_file = rs.data.invoice.pdf_file;
      const objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
      const win = window.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
      win.resizeTo(screen.availWidth, screen.availHeight);
      win.focus();
      this.spinner.hide();
    }
    else {
      /* Xem ha n t Client */
      let json_data = rs.data.invoice.json_data;
      let appCode = "REPX";
      if (rs.data.invoice.is_html_template)
        appCode = "HTML";
      this.socketService.xemhoadon(() => this.connectWebsocket(), json_data, appCode, "XEM_TRUOC");
    }
  }
  /* Kim tra M hng phi nhp khng du, khng cha k t c bit v ti a 50 k t */
  checkProductCode() {
    let isValid = true;
    const _lst = this.frmData.invoice_products.filter(p => p.product_name);
    if (_lst.length > 0) {
      for (let i = 0; i < _lst.length; i++) {
        if (VStringService._getValueToString(_lst[i].product_code) != "" && !this.productCodePattern.test(_lst[i].product_code)) {
          Library.notify(this._translate.VALIDATION.required_productCode, 'error', 5000);
          this.productGrip.dataGrid.instance.focus(this.productGrip.dataGrid.instance.getCellElement(i, 'product_code'));
          return false;
        }
      }
    }
    return isValid;
  }
  doctienbangchu(total_payment, currency_code) {
    if (VStringService._getValueToString(this.frmData.adjustment_object) == "TANG_GIAM" && this.isTT78) {
      let number_money = Math.abs(total_payment);
      let c_money = Helper.tienBangChu(number_money, VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);
      this.frmData.total_payment_in_word = c_money;
      if (total_payment < 0) {
        this.frmData.total_payment_in_word = "Gim " + c_money;
        if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
          this.frmData.total_payment_in_word = this.frmData.total_payment_in_word.replace("(", "(Adjusted to reduce by ")
        }
      }
      else if (total_payment > 0) {
        this.frmData.total_payment_in_word = "Tng " + c_money;
        if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
          this.frmData.total_payment_in_word = this.frmData.total_payment_in_word.replace("(", "(Adjusted to increase by ")
        }
      }
    }
    else {
      this.frmData.total_payment_in_word = Helper.tienBangChu(total_payment, VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);
      if (this.isTT78) {
        if (VStringService._getValueToString(this.frmData.total_payment_in_word) != "" && this.frmData.total_payment_in_word != "Khng ng chn.") {
          if (this.frmData.adjustment_form === 'TANG') {
            this.frmData.total_payment_in_word = "Tng " + this.frmData.total_payment_in_word;
            if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
              this.frmData.total_payment_in_word = this.frmData.total_payment_in_word.replace("(", "(Adjusted to increase by ")
            }
          }
          if (this.frmData.adjustment_form == 'GIAM') {
            this.frmData.total_payment_in_word = "Gim " + this.frmData.total_payment_in_word;
            if (this.configs.system_parameter.display_read_money_english && (currency_code == 'USD' || currency_code == 'EURO' || currency_code == 'EUR' || currency_code == 'MYR' || currency_code == 'CNY' || currency_code == 'SGD' || currency_code == 'JPY')) {
              this.frmData.total_payment_in_word = this.frmData.total_payment_in_word.replace("(", "(Adjusted to reduce by ")
            }
          }
        }
      }
    }
  }
  setTypeInvoice() {
    const template_code = this.frmData.template_code.split('/');
    if (template_code[0].indexOf('02GTTT') >= 0 || template_code[0].indexOf('07KPTQ') >= 0
      || template_code[0] == "2")
      this.isHdGTGT = false;
    else
      this.isHdGTGT = true;
  }
  onChangeSalePercen(e) {
    this.configs.system_parameter.value_rate_percent = e.value;
    let data = {
      config: {
        value: JSON.stringify(this.configs.system_parameter)
      }
    };
    this.configsService.update(this.configs.id.system_parameter, data).subscribe((response: any) => {
      if (response.status == "success") {
        this.configsService.getAll().subscribe((res) => {
          if (res.data && res.data.configs)
            this.authService.setConfigs(res.data.configs);
          this.configs = this.authService.getConfigs();
        });
      } else {
      }
    }, err => { }, () => {
    });
    this.onNumberTicketChange();
  }
