  onFileBangKeChange(evt) {
    const target: DataTransfer = <DataTransfer>(evt.target);
    if (target.files.length !== 1) {
      throw new Error('Khng th chn nhiu file');
    }
    const reader: FileReader = new FileReader();
    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      /* save data */
      const data = <any>(XLSX.utils.sheet_to_json(ws, { header: 'A', range: 9 }));
      const account = <any>(XLSX.utils.sheet_to_json(ws, { header: 'A', range: 0 }));
      const fileBangKe: any = document.getElementById('file-bang-ke');
      fileBangKe.value = '';
      let _arrData: any = [];
      if (data && data[0]) {
        let columnsName = data.map(e => e.A);
        let _index = columnsName.indexOf("view_order")

        if (_index == -1) {
          Library.notify("File mu khng ng nh dng. Vui lng nhn vo Ti y  ly li file mu", "error");
          return false;
        } else {
          if (_index > 0) {
            data.splice(0, _index);
          }
          for (let i = 1; i < data.length; i++) {
            let _row: any = {}
            for (const key in data[i]) {
              if (data[i].hasOwnProperty(key)) {
                const column = data[0][key]
                const value = data[i][key];
                _row[column] = value;
              }
            }
            _arrData.push(_row);
          }
        }
      }
      this.dataExcelFile = _arrData;
      this.dataExcelFileInfo = account.slice(0, 4);
      this.showPopup = true;
    };
    reader.readAsBinaryString(target.files[0]);
  }
  layMauBangKe() {
    Library.showloading();
    let ts = this._objTemplateSelect;
    let fields = [];
    if (!this.isTT78) {
      if (VStringService._getValueToString(this._objTemplateSelect.template_type) !== sEnum.templatetype.HoaDonVeMayBayTongHop
        && VStringService._getValueToString(this._objTemplateSelect.template_type) !== sEnum.templatetype.HoaDonVeMayBayChung
        && VStringService._getValueToString(this._objTemplateSelect.template_type) !== sEnum.templatetype.HoaDonVeMayBayHoanVe) {
        fields.push("other_tax");
        fields.push("other_charge");
      }
      if (VStringService._getValueToString(this._objTemplateSelect.template_type) !== sEnum.templatetype.HoaDonKinhDoanhXe) {
        fields.push("engine_number");
        fields.push("chassis_number");
      }
    }
    else {
      if (VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonVeMayBayTongHop
        && VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonVeMayBayChung
        && VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonVeMayBayHoanVe) {
        fields.push("other_tax");
        fields.push("other_charge");
      }
      if (VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonKinhDoanhXe) {
        fields.push("engine_number");
        fields.push("chassis_number");
      }
      if (VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonNhaHangKhachSan
        && VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonVeMayBayChung) {
        fields.push("is_money_service");
      }
      if (!this.configs.system_parameter.incurred_refund_ticket)
        fields.push("is_refund_ticket");
      if (!this.configs.system_parameter.view_expiration_date)
        fields.push("expiration_date");
      if (!this.configs.system_parameter.view_lot)
        fields.push("lot");
      if (!this.configs.system_parameter.promotional_goods_on_the_invoice)
        fields.push("is_promotion_new");
      if (!this.configs.system_parameter.is_promotion)
        fields.push("is_promotion");
    }
    if (!this.configs.system_parameter.product_exchange_rate)
      fields.push("product_exchange_rate");
    if (!this.configs.system_parameter.product_date)
      fields.push("product_date");
    // if (this.configs && this.configs.system_parameter && this.configs.system_parameter.display_contract_order)
    //   fields.push("contract_order");

    if (!this.extentionGrid || (this.extentionGrid && !this.extentionGrid.ma_quan_ly)) {
      fields.push("management_code");
    }
    let invoiceType = "";
    if (this.loaiHoaDon == sEnum.loaihoadon.XKNB03 || this.loaiHoaDon == sEnum.loaihoadon.HGDL04) {
      if (this.loaiHoaDon == sEnum.loaihoadon.XKNB03) {
        invoiceType = sEnum.loaihoadon.XKNB03
      } else {
        invoiceType = sEnum.loaihoadon.HGDL04
      }
    } else {
      invoiceType = ""
    }
    this.vatService.layFileMau({
      customer_name: this.oldForm.customer_name,
      buyer_tax_code: this.oldForm.buyer_tax_code,
      buyer_address: this.oldForm.buyer_address,
      buyer_name: this.oldForm.buyer_name,
      invoice_type_name: invoiceType,
      hiddens: fields
    }).subscribe(rs => {
      if (rs.status === 'success') {
        const file = rs.data.invoice;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }
