  _changeND92(e) {
    if (e.value)
      this.frmData.is_resolution_43 = !e.value;
    this._tinhGiaTri();
  }
  _changeNQ43(e) {
    if (e.value) {
      this.frmData.is_exemption_decree = !e.value;
      if (this.frmData.sales_percentage == null && parseInt(this.configs.system_parameter.value_rate_percent) > 0) {
        this.frmData.sales_percentage = parseInt(this.configs.system_parameter.value_rate_percent);
      }
    }
    else {
      this.frmData.sales_percentage = null;
    }
    this._tinhGiaTri();
  }
  cktmChange(row) {
    if (row.commercial_discount) {
      row.is_money_service = false;
    }
    this._tinhGiaTri();
  }
  onContentReady = (e) => {
    const arrFooter = [
      { name: this._translate.GRID.INVOICE_DETAILS.amount_after_vat, class: 'table-footer-1' },
      { name: this._translate.GRID.INVOICE_DETAILS.total_money, class: 'table-footer-1' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount_vat, class: 'table-footer-2' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount, class: 'table-footer-3' },
      { name: this._translate.GRID.INVOICE_DETAILS.amount_without_discount, class: 'table-footer-4' },
    ]
    this.invoiceService.formatSumColumnGrid(arrFooter);
  }
  editorOptionsForCode = {
    acceptCustomValue: true,
    showClearButton: true,
    onCustomItemCreating: (args) => {
      return this.invoiceService.addProductInSelecBox(args, this.select.productsView, this.dataGrid);
    }
  }
