  checkDataFormChange(dataChange, dataForm) {
    const dataBefore = JSON.parse(dataChange);
    const dataAfter = JSON.parse(JSON.stringify(dataForm));
    return _.isEqual(dataBefore, dataAfter);
  }

  async subData(type, value) {
    const checkValid = this.formInvoice.form.instance.validate();
    if (!checkValid.isValid) {
      return false;
    }
    if (this.windowPreviewBeforeSubmit) {
      this.windowPreviewBeforeSubmit.focus();
      return false;
    }
    await this.handleFormDataSub();
    if (this.frmData.invoice_options) {
      this.frmData.invoice_options.forEach(item => {
        delete item['is_show'];
        delete item['type'];
        delete item['width'];
        delete item['statusData'];
      });
    }
    if (this.frmData.total_payment > 20000000 && (this.frmData.payment_method_name === 'TM')) {
      Library.notify(this._translate.NOTIFY.payment_method_more_than_20_million, 'error');
      return false;
    }
    if (!this.isAccessExportInv) {
      if (type === 'TRINH_KY') {
        this.frmData.approve_status = sEnum.statusApprove.ChoDuyet;
        this.frmData.refuse_reason = '';
      } else {
        if (this.frmData.approve_status !== sEnum.statusApprove.TuChoi) {
          this.frmData.approve_status = sEnum.statusApprove.ChuaTrinhKy;
        }
      }
    }
    if (value.id) {
      Library.showloading();
      if (!this.isTT78) {
        return await this.vatService.edit(this.frmData.id, value).toPromise().then(res => {
          Library.hideloading();
          if (res.status === 'success') {
            Library.notify(this._translate.NOTIFY.update_success, 'success');
            return true;
          } else {
            Library.notify(res.message, 'error');
            return false;
          }
        });
      }
      else {
        return await this.vatService.editTT78(this.frmData.id, value).toPromise().then(res => {
          Library.hideloading();
          if (res.status === 'success') {
            Library.notify(this._translate.NOTIFY.update_success, 'success');
            return true;
          } else {
            Library.notify(res.message, 'error');
            return false;
          }
        });
      }
    } else {
      Library.showloading();
      if (!this.isTT78) {
        return await this.vatService.ghiTam(value).toPromise().then(res => {
          Library.hideloading();
          if (res.status === 'success') {
            Library.notify(this._translate.NOTIFY.update_success, 'success');
            return true;
          } else {
            Library.notify(res.message, 'error');
            return false;
          }
        });
      }
      else {
        return await this.vatService.ghiTamTT78(value).toPromise().then(res => {
          Library.hideloading();
          if (res.status === 'success') {
            Library.notify(this._translate.NOTIFY.update_success, 'success');
            return true;
          } else {
            Library.notify(res.message, 'error');
            return false;
          }
        });
      }
    }
  }

  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    if (!this.checkDataFormChange(this.valueChange, this.frmData)) {
      return new Observable<boolean>(
        (observer) => {
          this.confirmService.confirm().subscribe(
            value => {
              switch (value) {
                case 'yes':
                  this.subData('GHI_TAM', this.frmData).then(value1 => {
                    observer.next(value1);
                    observer.complete();
                  });
                  break;
                case 'no':
                  observer.next(true);
                  observer.complete();
                  break;
                case 'cancel':
                  observer.next(false);
                  observer.complete();
                  break;
                default:
                  observer.next(true);
                  observer.complete();
                  break;
              }
            }
          );
        });
    }
    return true;
  }
